// ------------------------------------------------ \\
// $CUSTOM COLOR PALETTE
//
// Utility classes + variables
// ------------------------------------------------
// Define default, darker and lighter variants
// (10 to 90 percent) for:
// ------------------------------------------------
// * backgrounds
// * headings
// * links
//
// Imported in `m2m.scss`
// ------------------------------------------------ //

// Defaults
$background:#ebd5c9 !default;
$heading: #014c5d !default;
$link: #98d1dd !default;

$m2m-bg-color-base: $background;
$m2m-heading-color-base: $heading;
$m2m-link-color-base: $link;

// Map for colors
$m2m-colors: (
  heads: (
    base: $heading,
    l-10: tint-color($heading, 10%),
    l-20: tint-color($heading, 20%),
    l-30: tint-color($heading, 30%),
    l-40: tint-color($heading, 40%),
    l-50: tint-color($heading, 50%),
    l-60: tint-color($heading, 60%),
    l-70: tint-color($heading, 70%),
    l-80: tint-color($heading, 80%),
    l-90: tint-color($heading, 90%),
    d-10: shade-color($heading, 10%),
    d-20: shade-color($heading, 20%),
    d-30: shade-color($heading, 30%),
    d-40: shade-color($heading, 40%),
    d-50: shade-color($heading, 50%),
    d-60: shade-color($heading, 60%),
    d-70: shade-color($heading, 70%),
    d-80: shade-color($heading, 80%),
    d-90: shade-color($heading, 90%)
  ),
  link: (
    base: $link,
    l-10: tint-color($link, 10%),
    l-20: tint-color($link, 20%),
    l-30: tint-color($link, 30%),
    l-40: tint-color($link, 40%),
    l-50: tint-color($link, 50%),
    l-60: tint-color($link, 60%),
    l-70: tint-color($link, 70%),
    l-80: tint-color($link, 80%),
    l-90: tint-color($link, 90%),
    d-10: shade-color($link, 10%),
    d-20: shade-color($link, 20%),
    d-30: shade-color($link, 30%),
    d-40: shade-color($link, 40%),
    d-50: shade-color($link, 50%),
    d-60: shade-color($link, 60%),
    d-70: shade-color($link, 70%),
    d-80: shade-color($link, 80%),
    d-90: shade-color($link, 90%)
  )
);
// Map for background colors
$m2m-bg-colors: (
  bg: (
    base: $background,
    l-10: tint-color($background, 10%),
    l-20: tint-color($background, 20%),
    l-30: tint-color($background, 30%),
    l-40: tint-color($background, 40%),
    l-50: tint-color($background, 50%),
    l-60: tint-color($background, 60%),
    l-70: tint-color($background, 70%),
    l-80: tint-color($background, 80%),
    l-90: tint-color($background, 90%),
    d-10: shade-color($background, 10%),
    d-20: shade-color($background, 20%),
    d-30: shade-color($background, 30%),
    d-40: shade-color($background, 40%),
    d-50: shade-color($background, 50%),
    d-60: shade-color($background, 60%),
    d-70: shade-color($background, 70%),
    d-80: shade-color($background, 80%),
    d-90: shade-color($background, 90%)
  ),
);

// Print all the utility classes
#{$site-prefix} {
  @include m2m-color($m2m-bg-colors, 'background-color', $separator: '-');
  @include m2m-color($m2m-colors, 'color', $separator: '-');
}


